"use client";

import React, { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Loader2, Plus, Folder, Calendar, ExternalLink, Eye } from "lucide-react";
import { useAuth } from "@/lib/auth-context";
import { getUserProjects, Project } from "@/lib/projectService";
import Link from "next/link";

const statusColors = {
  draft: "bg-gray-100 text-gray-800",
  building: "bg-yellow-100 text-yellow-800",
  deployed: "bg-green-100 text-green-800",
  failed: "bg-red-100 text-red-800",
};

const statusIcons = {
  draft: "📝",
  building: "🔨",
  deployed: "🚀",
  failed: "❌",
};

export default function ProjectsPage() {
  const { user, loading: authLoading } = useAuth();
  const [projects, setProjects] = useState<Project[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  useEffect(() => {
    if (!authLoading && user) {
      fetchProjects();
    } else if (!authLoading && !user) {
      setLoading(false);
    }
  }, [user, authLoading]);

  const fetchProjects = async () => {
    try {
      setLoading(true);
      const result = await getUserProjects();
      
      if (result.success && result.projects) {
        setProjects(result.projects);
      } else {
        setError(result.error || "Failed to fetch projects");
      }
    } catch (err) {
      setError("An error occurred while fetching projects");
    } finally {
      setLoading(false);
    }
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
    });
  };

  if (authLoading || loading) {
    return (
      <div className="flex-1 flex items-center justify-center p-4">
        <div className="text-center">
          <Loader2 className="w-8 h-8 animate-spin mx-auto mb-4" />
          <p className="text-gray-600">Loading projects...</p>
        </div>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="flex-1 flex items-center justify-center p-4">
        <div className="text-center">
          <Folder className="w-16 h-16 mx-auto mb-4 text-gray-400" />
          <h2 className="text-xl font-semibold mb-2">Sign in to view projects</h2>
          <p className="text-gray-600 mb-4">You need to be signed in to access your projects.</p>
        </div>
      </div>
    );
  }

  return (
    <div className="flex-1 space-y-6 p-4 md:p-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Projects</h1>
          <p className="text-gray-600">Manage your AI-powered projects</p>
        </div>
        <Link href="/">
          <Button className="bg-[#FF5C5C] hover:bg-[#E54545]">
            <Plus className="mr-2 h-4 w-4" />
            New Project
          </Button>
        </Link>
      </div>

      {error && (
        <div className="bg-red-50 border border-red-200 rounded-lg p-4">
          <p className="text-red-600">{error}</p>
          <Button 
            variant="outline" 
            size="sm" 
            onClick={fetchProjects}
            className="mt-2"
          >
            Try Again
          </Button>
        </div>
      )}

      {projects.length === 0 && !error ? (
        <div className="text-center py-12">
          <Folder className="w-16 h-16 mx-auto mb-4 text-gray-400" />
          <h2 className="text-xl font-semibold mb-2">No projects yet</h2>
          <p className="text-gray-600 mb-4">Create your first AI project to get started.</p>
          <Link href="/">
            <Button className="bg-[#FF5C5C] hover:bg-[#E54545]">
              <Plus className="mr-2 h-4 w-4" />
              Create Project
            </Button>
          </Link>
        </div>
      ) : (
        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
          {projects.map((project) => (
            <Card key={project.id} className="hover:shadow-lg transition-shadow">
              <CardHeader>
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <CardTitle className="text-lg">{project.name}</CardTitle>
                    <CardDescription className="mt-1">
                      {project.description}
                    </CardDescription>
                  </div>
                  <Badge 
                    className={`ml-2 ${statusColors[project.status]}`}
                    variant="secondary"
                  >
                    {statusIcons[project.status]} {project.status}
                  </Badge>
                </div>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="space-y-2">
                  <div className="flex items-center text-sm text-gray-600">
                    <span className="font-medium">Template:</span>
                    <span className="ml-2 capitalize">{project.template}</span>
                  </div>
                  <div className="flex items-center text-sm text-gray-600">
                    <span className="font-medium">Platform:</span>
                    <span className="ml-2 capitalize">{project.deployPlatform}</span>
                  </div>
                  <div className="flex items-center text-sm text-gray-600">
                    <Calendar className="w-4 h-4 mr-1" />
                    <span>{formatDate(project.createdAt)}</span>
                  </div>
                </div>

                <div className="space-y-2">
                  <p className="text-sm font-medium">Modules:</p>
                  <div className="flex flex-wrap gap-1">
                    {project.modules.map((module, index) => (
                      <Badge key={index} variant="outline" className="text-xs">
                        {module.replace('-', ' ')}
                      </Badge>
                    ))}
                  </div>
                </div>

                <div className="flex gap-2 pt-2">
                  <Link href={`/projects/${project.id}`} className="flex-1">
                    <Button variant="outline" size="sm" className="w-full">
                      <Eye className="w-4 h-4 mr-1" />
                      View Details
                    </Button>
                  </Link>
                  {project.deployUrl && (
                    <Button 
                      variant="outline" 
                      size="sm"
                      onClick={() => window.open(project.deployUrl, '_blank')}
                    >
                      <ExternalLink className="w-4 h-4" />
                    </Button>
                  )}
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  );
}
