import { useAuth } from "./auth-context";

export interface CreateProjectData {
  name: string;
  template: string;
  modules: string[];
  deployPlatform: string;
  description?: string;
}

export interface Project {
  id: string;
  name: string;
  template: string;
  modules: string[];
  deployPlatform: string;
  description: string;
  status: 'draft' | 'building' | 'deployed' | 'failed';
  userId: string;
  createdAt: string;
  updatedAt: string;
  deployUrl?: string;
}

export async function createProject(projectData: CreateProjectData): Promise<{ success: boolean; projectId?: string; project?: Project; error?: string }> {
  try {
    // Get current user token (in a real app, you'd get the Firebase ID token)
    const token = 'mock_token'; // In real app: await user.getIdToken()

    const response = await fetch('/api/projects/new', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`,
      },
      body: JSON.stringify(projectData),
    });

    const data = await response.json();

    if (!response.ok) {
      throw new Error(data.error || 'Failed to create project');
    }

    return {
      success: true,
      projectId: data.projectId,
      project: data.project,
    };
  } catch (error) {
    console.error('Error creating project:', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error occurred',
    };
  }
}

